@import url("https://fonts.googleapis.com/css?family=Lora:400,400i,700|Raleway:700");

h1,
h2,
h3,
h4,
a.link {
  font-family: "Raleway", Helvetica, Verdana, sans-serif;
  font-weight: 700;
}

.global-header h1,
.nav a,
.links a {
  font-family: "Raleway", Helvetica, Verdana, sans-serif;
  font-weight: 700;
}

body {
  font-family: "Lora", Times, serif;
  font-weight: 400;
  font-size: 18px;
  text-align: left;

  @include lineHeight(18px);
}

@media (max-width: $mobileBreakpointSmall) {

  body {
    font-size: 16px;

    @include lineHeight(16px);
  }

}
